body {
  margin: 0;
  font-family: 'Lato', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.non-auth-background {
    width: 100%;
    min-height: 100vh;
    background-image: url(../../static/media/backgroundImage.662acd81.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
}


.login-app-logo {
    
    width: 100%;
    max-height: 100px;
    object-fit: cover;



}
.flexprop{
    margin-bottom: 5%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    size: 10%;
}

.flexprop>* {
    flex: 1 1 160px;
}


.app-logo-white {
    padding-top: 4%;
    object-fit: contain;
    width: 180px;
    height: 60px;
    transition: transform .3s;
}

